.App{
  background-color: rgb(105, 105, 177);
  width: 100%;
  height: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container{
  background-color: rgb(164, 199, 201);
  width: 500px;
  padding: 50px;
  border-radius: 30px;
  box-shadow: 0 4px 8px 0 rebeccapurple;
}

h1{
  text-decoration: underline;
  color: rgb(119, 0, 255);
}

p{
  color: rgb(64, 13, 116);
  font-weight: bolder;
}

.total{
  display: flex;
  justify-content:center;
  align-items: center;
  flex-direction: column;
  background-color: rgb(221, 221, 96);
  height: 200px;
  border-radius: 20px;
  margin: 20px;
  box-shadow: 0 4px 8px 0 blue;
}

.total h2{
  color: rgb(10, 61, 214);
}

.input{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px;
}

.button{
  display: flex;
  justify-content: space-around;
}